import * as React from "react"

import Layout from "../../components/layout"
import Seo from "../../components/seo"
import "./donate.css"
import { StaticImage } from "gatsby-plugin-image"

const IndexPage = () => (
    <Layout page="donate">
      <Seo title="Donate" />
      <section className="section">
        <div className="widthLimit-donate">
          <div className="columns">
            <div className="column is-two-fifths">
                <StaticImage src="../../images/donate-img.png"
                             alt="How We Allocate Our Donation"
                             className="donate-img"
                             width={464}
                             height={1106}
                />
            </div>
            <div className="column">
              <strong className="donate-title">SUPPORT OUR CAUSE</strong>
              <span className="donate-h2">Leave a donation, make a change today!</span>
              <span className="donate-h3"><strong>100%</strong> of your donation will go back to the community</span>
              <em className="donate-h4">If you need help or have questions about our donation process, you can fill out the donation form below. We will have a staff to assist you within 72 business hours.</em>
              <div className="donation-button">
                <a href="https://www.paypal.com/donate/?hosted_button_id=5HTYA6L579HV8" className="donation-link">DONATION</a>
              </div>
              <iframe
                src="https://docs.google.com/forms/d/e/1FAIpQLSe9NUg9j5potfuWOcyJs5TdqOnC2laTAgr43Un5Cg1G9OiMCg/viewform?embedded=true"
                title="Donation Form"
                width="100%"
                height="386"
                style={{
                  paddingTop: "24px"
                }}
              >Loadingâ€¦
              </iframe>
              <em className="donate-qr-text">You can also scan this QR code to make a donation through PayPal</em>
              <div className="donate-paypal-QR">
                <StaticImage src="../../images/PaypalQR.png" alt="Paypal QR Code" />
              </div>
            </div>
          </div>
        </div>
      </section>
    </Layout>
)

export default IndexPage
